import { useState, useEffect } from "react";
import { Link, useParams, NavLink, useNavigate } from 'react-router-dom';
import { Breadcrumb, SimpleCard } from "app/components";
import { LoadingButton } from '@mui/lab';
import { Formik, Form, Field, FieldArray } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import swal from 'sweetalert';
import {
  Card,
  Divider,
  CardContent,
  CardHeader,
  Box,
  Grid,
  Icon,
  IconButton,
  Button,
  styled,
  TextField,
  useTheme,
  Table, TableHead, TableRow, TableCell, TableBody
} from "@mui/material";
import Moment from 'moment';

const Item = styled("div")(({ theme }) => (
  {
    marginBottom: "5px",
    fontSize: '16px',
    fontWeight: '400',
    color: '#484747'
  }
));
const ItemContent = styled("div")(({ theme }) => (
  {
    marginBottom: "15px",
    fontSize: '16px',
    fontWeight: '600',
  }
));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));
const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

// form field validation schema
const validationSchema = Yup.object().shape({
  status: Yup.string()
    .required('User Accton is required!'),
});

const ViewMedicinesStock = () => {

  const { row_id } = useParams();
  const theme = useTheme();
  const navigate = useNavigate();
  const [medicine, setMedicine] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/get-medicine', {
          row_id: row_id
        })
          .then(response => {
            if (response.data.response_code === 200) {
              setMedicine(response.data.data);
            } else if (response.data.response_code === 401) {

            } else {

            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  return (
    <>
      <Container>
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Medicine Details" }]} />
        </Box>

        <Card title="Medicine Details">
          <ContentBox>
            <CardHeader
              title="Medicine Details"
              titleTypographyProps={{
                variant: "h4",
                fontSize: '18px',
                fontWeight: '600',
                // textTransform: 'uppercase',
                color: theme.palette.primary.main,

              }}
              action={
                <>
                  <Button title="Edit" component={Link}
                    to={`/dashboard/medicine/edit-stock/${row_id}`}
                    color="primary" variant="outlined" size="small" sx={{ m: .5 }}>
                    Edit
                  </Button>
                  <Button title="Back" component={Link}
                    to={`/dashboard/medicine/list`}
                    color="inherit" variant="outlined" size="small" sx={{ m: .5 }}>
                    Back
                  </Button>
                </>
              }
            />
            <Divider />
            <CardContent>
              <Box width="100%" overflow="auto">
                {medicine && (
                  <>
                    <Grid container>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Product Name</Item>
                        <ItemContent>{medicine.product_name}</ItemContent>
                      </Grid>

                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Category</Item>
                        <ItemContent>{medicine.category_name}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Brand</Item>
                        <ItemContent>{medicine.brand_name}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Unit Packing</Item>
                        <ItemContent>{medicine.unit_packing}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Expiry Date</Item>
                        <ItemContent>{Moment(medicine.expiry_date).format('DD/MM/YYYY')}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Available Quantity</Item>
                        <ItemContent>{medicine.available_qty}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>HSN/SAC Code</Item>
                        <ItemContent>{medicine.hsn_sac_code}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Batch No.</Item>
                        <ItemContent>{medicine.batch_no}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>MRP</Item>
                        <ItemContent>{medicine.mrp}</ItemContent>
                      </Grid>

                    </Grid>
                    <Divider />
                    <Grid container>
                      <Grid item sm={6} xs={12} lg={6}>
                        <Item>Description</Item>
                        <ItemContent>{medicine.description}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={6}>
                        <Item>Product Image</Item>
                        <ItemContent><img src={medicine.product_image} style={{ width: '100px' }} /></ItemContent>
                      </Grid>
                    </Grid>
                    <br />
                    <br />
                    <Divider />

                    <Grid container>
                      <Grid item sm={6} xs={12} lg={2}>
                        <Item>Quantity</Item>
                        <ItemContent>{medicine.min_qty}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={2}>
                        <Item>Discount (%)</Item>
                        <ItemContent>{medicine.min_qty_discount}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={2}>
                        <Item title="Dicount Min Bonus">Bonus (Deal)</Item>
                        <ItemContent>{medicine.min_qty_bonus_deal}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={2}>
                        <Item title="Trade Rate">Trade Rate</Item>
                        <ItemContent>{medicine.min_qty_trade_rate}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={2}>
                        <Item>Net Rate</Item>
                        <ItemContent>{medicine.min_qty_net_rate}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={2}>
                        <Item title="Min Qty Margin Percentage">Margin (%)</Item>
                        <ItemContent>{medicine.min_qty_margin_percentage}</ItemContent>
                      </Grid>

                    </Grid>
                    <Divider />
                    <br />
                    <br />
                    <h4 style={{ color: theme.palette.primary.main, fontSize: '18px', }}>Tax Category</h4>
                    <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                      <Grid item sm={12} xs={12} lg={8}>
                        <Box overflow="auto">
                          <Table>
                            <TableHead>
                              <TableRow>
                                <TableCell>CGST</TableCell>
                                <TableCell>SGST</TableCell>
                                <TableCell>IGST</TableCell>
                                <TableCell>CESS</TableCell>
                              </TableRow>
                            </TableHead>
                            <TableBody>
                              <TableRow>
                                <TableCell align="left">{medicine.cgst}</TableCell>
                                <TableCell align="left">{medicine.sgst}</TableCell>
                                <TableCell align="left">{medicine.igst}</TableCell>
                                <TableCell align="left">{medicine.cess}</TableCell>
                              </TableRow>
                            </TableBody>
                          </Table>
                        </Box>
                      </Grid>
                    </Grid>
                  </>
                )}
              </Box>
            </CardContent>
          </ContentBox>
        </Card>

      </Container>
    </>

  );
};

export default ViewMedicinesStock;
